<template>
  <component :is="currentBlog"></component>
</template>

<script>
const whiteList = ['eggiverse', 'test']

export default {
  name: 'Blog',
  data () {
    return {
      currentBlog: 'test'
    }
  },
  components: {
    'eggiverse': () => import('../../docs/blog_eggiverse.md'),
    'test': () => import('../../docs/blog_test.md')
  },
  beforeRouteEnter (to, from, next) {
    if (whiteList.indexOf(to.params.blogname) === -1) {
      next('/404')
    } else {
      next(vm => {
        vm.currentBlog = to.params.blogname
      })
    }
  },
  beforeRouterUpdate (to, from, next) {
    if (whiteList.indexOf(to.params.blogname) === -1) {
      next('/404')
    } else {
      this.currentBlog = to.params.blogname
      next()
    }
  }
}
</script>

<style lang="scss">
  @import "../styles/blog.scss"
</style>



