import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import MyTest from '@/components/MyTest'
import MyAppToc from '@/components/MyAppToc'
import MyApp from '@/components/MyApp'
import NotFound from '@/components/NotFound'
import Blog from '@/components/Blog'
import BlogToc from '@/components/BlogToc'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/mytest',
      name: 'mytest',
      component: MyTest
    },
    {
      path: '/app',
      name: 'myappToc',
      component: MyAppToc
    },
    {
      path: '/app/:appname',
      name: 'myapp',
      component: MyApp
    },
    {
      path: '/blog',
      name: 'blogToc',
      component: BlogToc
    },
    {
      path: '/blog/:blogname',
      name: 'blog',
      component: Blog
    },
    {
      path: '/404',
      name: '404',
      component: NotFound
    },
    {
      path: '*',
      redirect: '/404'
    }
  ]
})



