<template>
  <div>
    <ul>
      <li :key="blog.num" v-for="blog in blogList">
        <router-link :to="'/blog/' + blog.name">{{ blog.name }}</router-link>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'BlogToc',
  data () {
    return {
      blogList: [
        {name: 'eggiverse', num: 1},
        {name: 'test', num: 2}
      ]
    }
  }
}
</script>

<style lang="scss" scoped>
@import "../styles/toc.scss"
</style>



