<template>
  <div>
    <h1> {{$route.params.appname}} </h1>
    <component :is="currentApp"></component>
  </div>
</template>

<script>
const whiteList = ['ColourMatrix', 'CountingDevice']

export default {
  name: 'MyApp',
  data () {
    return {
      currentApp: ''
    }
  },
  components: {
    'cmprc': () => import('../../docs/colourmatrix_privacy.md'),
    'cdprc': () => import('../../docs/countingdevice_privacy.md')
  },
  beforeRouteUpdate (to, from, next) {
    if (whiteList.indexOf(to.params.appname) === -1) {
      next('/404')
    } else {
      this.currentApp = to.params.appname === 'ColourMatrix' ? 'cmprc' : 'cdprc'
      console.log(this.currentApp)
      next()
    }
  },
  beforeRouteEnter (to, from, next) {
    if (whiteList.indexOf(to.params.appname) === -1) {
      next('/404')
    } else {
      next(vm => {
        vm.currentApp = to.params.appname === 'ColourMatrix' ? 'cmprc' : 'cdprc'
        console.log(vm.currentApp)
      })
    }
  }
}
</script>

<style lang="scss">
  @import "../styles/blog.scss"
</style>



