  img {
    width: 100px;
    display: inline-block;
  }

  ul {
    margin: 0;
    list-style-type: none;
    overflow: hidden;
    display: flex;
    padding: 0;
  }

  li {
    float: left;
    a {
      display: block;
      color: $bar-text;
      text-align: center;
      padding: 14px 16px;
      text-decoration: none;
      &:hover {
