<template>
  <div>
    <ul>
      <li :key="app.num" v-for="app in appList">
        <router-link :to="'/app/' + app.name">{{ app.name }}</router-link>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'MyAppToc',
  data () {
    return {
      appList: [
        {name: 'ColourMatrix', num: 1},
        {name: 'CountingDevice', num: 2}
      ]
    }
  }
}
</script>

<style lang="scss" scoped>
@import "../styles/toc.scss"
</style>



